import { Game } from "../model/Game.ts";
import { isPastDate } from "../date";
import {fetchApi} from "../fetch.ts";
import {LeagueRepository} from "./LeagueRepository.ts";
import {TeamRepository} from "./TeamRepository.ts";
import { FieldRepository } from "./FieldRepository.ts";

type ApiGame = {
  venue: string;
  home: string;
  away: string;
  awayScore?: number;
  homeScore?: number;
  status: "scheduled" | "finished" | "canceled";
  date: string;
  league: string;
  season: number;
};

export abstract class GamesRepository {
  public static async findScheduledBySeasonAndLeague(season: number, league: string, onlyFutureGames: boolean): Promise<Game[]> {
    const apiGames = ( await fetchApi(`/api/seasons/${season}/${league}/games.json`, () => [])) as ApiGame[];
    const result: Game[] = [];

    for (const apiGame of apiGames) {
      const gameDateTime = new Date(apiGame.date);
      const includeGame = onlyFutureGames ? !isPastDate(gameDateTime) && apiGame.status === "scheduled" : true;

      if (includeGame) {
          result.push({
            home: (await TeamRepository.findByName(apiGame.home)),
            away: (await TeamRepository.findByName(apiGame.away)),
            homeScore: apiGame.homeScore,
            awayScore: apiGame.awayScore,
            venue: apiGame.venue ? (await FieldRepository.findByKeyword(apiGame.venue)) : undefined,
            date: gameDateTime,
            league: await LeagueRepository.findById(apiGame.league),
          });
      }
    }

    return Object.values(result).sort((a, b) => a.date.getTime() - b.date.getTime());
  }
}
