import {BeforeInstallPromptEvent} from "../types";

let installPromptEvent: BeforeInstallPromptEvent|undefined = undefined;

export async function isPWAInstalled(): Promise<boolean> {
    // Check for iOS Safari
    if (
        window.matchMedia("(display-mode: standalone)").matches ||
        ("standalone" in window.navigator && window.navigator.standalone)
    ) {
        return true;
    }

    // Check for other platforms (Android, Chrome, etc.)
    if (
        "getInstalledRelatedApps" in navigator &&
        typeof navigator.getInstalledRelatedApps === "function"
    ) {
        const relatedApps = await navigator.getInstalledRelatedApps();
        return relatedApps.length > 0;
    }

    return false;
}

export function registerInstallPromptListener() {
    window.addEventListener("beforeinstallprompt", (event) => {
        event.preventDefault();

        installPromptEvent = (event as BeforeInstallPromptEvent);

        console.log("Install prompt event detected");
    })
}

export function getInstallPromptEvent(timeout: number = 5000): Promise<BeforeInstallPromptEvent | undefined> {
    return new Promise((resolve) => {
        const startTime = Date.now();

        const checkEvent = () => {
            if (installPromptEvent) {
                resolve(installPromptEvent); // Resolve immediately if event is available
            } else if (Date.now() - startTime < timeout) {
                setTimeout(checkEvent, 100); // Check every 100ms until timeout
            } else {
                resolve(undefined); // Resolve with undefined after timeout
            }
        };

        checkEvent(); // Start checking immediately
    });
}

export function registerServiceWorker(): void {
    if ("serviceWorker" in navigator) {
        window.addEventListener("load", () => {
            const swFile = '/service-worker.js'
            const scope = '/'

            navigator.serviceWorker
                .register(swFile, { scope, type: 'module' })
                .then((registration) => {
                    console.log(
                        "ServiceWorker registration successful with scope: ",
                        registration.scope,
                    );
                })
                .catch((error) => {
                    console.log("ServiceWorker registration failed: ", error);
                });
        });
    }
}
