import type {Event} from "../model/Event"
import {fetchApi} from "../fetch.ts";
import {CONFIG} from "../config.ts";
import {isPastDate} from "../date.ts";

type ApiEvent = {
    title: string;
    description?: string;
    date: string;
    venue: string;
    logo?: string;
};


export class EventRepository {
    public static async findAll(onlyFutureEvents: boolean): Promise<Event[]> {
        const apiEvents = (await fetchApi(`/api/events.json`, () => [])) as ApiEvent[];

        return apiEvents.map(v => {
            return {
                ...v,
                logo: `${CONFIG.API_BASE}assets/events/${v.logo}`,
                date: new Date(v.date)
            }
        })
            .filter(v => onlyFutureEvents ? !isPastDate(v.date) : true)
            .sort((a, b) => a.date.getTime() - b.date.getTime())
    }
}
