import { Route } from "./Route.ts";

export class Router extends HTMLElement {
    static routes: Route[] = [];

    constructor() {
        super();
        window.addEventListener('hashchange', () => this.update());

        this.update();
    }

    public static unregister(route: Route) {
        this.routes = this.routes.filter(v => v !== route);
    }

    public static register(route: Route) {
        this.routes.push(route);

        if (route.isMatchingPath()) {
            route.show();
        } else {
            route.hide();
        }
    }

    public static current(): string {
        return window.location.hash.slice(1); // Remove the '#' symbol
    }

    private update() {
        Router.routes.forEach((route) => {
            if (route.isMatchingPath()) {
                route.show();
            } else {
                route.hide();
            }
        });
    }

    connectedCallback() {
        this.update(); // Trigger initial route on page load
    }

    public static default(): Route|undefined
    {
        return Router.routes.find((route) => route.isDefault);
    }

    public static exists(path: string): boolean
    {
        return Router.routes.some((route) => route.path === path);
    }
}
