import type { Field } from "../model/Field"
import {fetchApi} from "../fetch.ts";
import {CONFIG} from "../config.ts";
type ApiField = {
    teams: string[];
    location: string;
    venue: string|null;
    image?: string|null;
    keywords: string[];
};

export class FieldRepository {
    private static FIELD_CACHE: { [key: string]: Field } = {};

    public static async findAll(): Promise<Field[]> {
        const apiFields = (await fetchApi(`/api/fields.json`, () => [])) as ApiField[];

        return apiFields.map(v  => {
            return {
                teams: v.teams,
                location: v.location,
                venue: v.venue,
                image: v.image ? `${CONFIG.API_BASE}assets/fields/${v.image}` : undefined,
                keywords: v.keywords
            }
        })
    }

    public static async findByKeyword(keyword: string): Promise<Field|undefined> {
        let cachedField = FieldRepository.FIELD_CACHE[keyword];
    
        if (cachedField) {
            return cachedField
        }
    
        if (keyword.trim() === ",") {
            return undefined
        }
    
        const field = (await FieldRepository.findAll()).find((field) => {
            return field.keywords.includes(keyword)
        }) || undefined;
    
        if (field) {
            FieldRepository.FIELD_CACHE[keyword] = field;
        }
    
        return field;
    }
}
