import {dateTimeFormatter} from "./date.ts";

export class EventCard extends HTMLElement {
    static observedAttributes = ["title", "description", "date", "venue", "logo"];
    private shadow: ShadowRoot;

    constructor() {
        super();

        this.shadow = this.attachShadow({mode: 'open'});
        const style = document.createElement("style");

        style.textContent = `
          :host {
            display: block;
            width: 100%;
            min-width: 370px;
          }
        `;

        this.shadow.appendChild(style);

        this.update();
    }

    attributeChangedCallback(
        name: string,
        oldValue: string | null,
        newValue: string | null,
    ) {
        if (EventCard.observedAttributes.includes(name) && oldValue !== newValue) {
            this.update();
        }
    }

    private update() {
        const title = this.getAttribute('title') || '';
        const description = this.getAttribute('description') || undefined;
        const logo = this.getAttribute('logo') || undefined;
        const venue = this.getAttribute('venue');
        const dateString = this.getAttribute('date') || new Date('1970');
        const date = new Date(dateString);

        this.shadow.innerHTML = `
            <x-card>
                ${logo ? `<x-card-media src=${logo}></x-card-media>` : ''}
                <x-card-content>
                    <x-stack direction="vertical" gap="small">
                        <x-box justify-content="left">
                            <x-typography display="h2">
                                ${title}
                            </x-typography>
                        </x-box>
                        <x-stack direction="vertical" >
                            
                            <x-box justify-content="left">
                                <x-typography display="h4" style="font-weight: 600">
                                    ${dateTimeFormatter.format(date)}
                                </x-typography>
                            </x-box>
                            <x-box justify-content="left">
                                <x-typography display="h5" style="opacity: 0.5">
                                    ${venue}
                                </x-typography>
                            </x-box>
                            
                            
                        </x-stack>
                        
                        ${
                            description ?
                                `<x-box justify-content="left">
                                    <x-typography display="body1">
                                    ${description}
                                    </x-typography>
                                </x-box>` : ''
                        }
                    </x-stack>
                </x-card-content>
            </x-card>
        `;
    }
}

