import { Router } from "./Router.ts";
import { Storage } from "../storage/Storage"; // Assuming Storage is globally accessible

export class Route extends HTMLElement {
    static get observedAttributes() {
        return ['path', 'default'];
    }

    private lastState: string | null = null;

    connectedCallback() {
        Router.register(this);

        // Check if the current route matches any of the comma-separated paths
        if (!this.isMatchingPath()) {
            this.style.display = 'none';
        }

        if (Router.current() === '' && this.isDefault) {
            this.style.display = 'block';
        }
    }

    disconnectedCallback() {
        Router.unregister(this);
    }

    get path() {
        return this.getAttribute('path') || '';
    }

    get isDefault() {
        return this.hasAttribute('default');
    }

    set path(value: string) {
        this.setAttribute('path', value);
    }

    public isMatchingPath() {
        const paths = this.path.split(',').map(p => p.trim());
        return paths.includes(Router.current());
    }

    show() {
        this.style.display = 'block';

        const currentState = Storage.getStateString();

        if (this.lastState !== currentState) {
            // If the state has changed, clone and replace the content
            this.lastState = currentState;

            this.childNodes.forEach((v) => {
                const clone = v.cloneNode(true);
                this.replaceChild(clone, v);
            });
        }

    }

    hide() {
        this.style.display = 'none';
    }
}
