import { Snackbar, BareLink, BottomNavigationAction } from '@arjanfrans/web-components-ui';
import { translate } from './translations'
import { Router } from './router/Router';

export class Shell extends HTMLElement {
    private shadow: ShadowRoot;
    private snackbarContainer: HTMLDivElement;

    constructor() {
        super();
        this.shadow = this.attachShadow({ mode: 'open' });

        this.shadow.innerHTML = `
            <style>
                :host {
                    display: flex;
                    flex-direction: column;
                    height: 100vh;
                    position: relative;
                }

                #offline-snackbar {
                    display: none;
                }

                /* Splash screen overlay */
                #splash {
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: white; /* Adjust splash background as needed */
                    z-index: 99999;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    transition: opacity 1s ease-out;
                    pointer-events: none;
                }

                #splash.fade-out {
                    opacity: 0;
                }

            </style>

            <!-- Splash screen overlay -->
            <hb-splash id="splash"></hb-splash>

            <!-- Main content (disabled while splash is active) -->
            <hb-header></hb-header>
            <x-scroll-box stretch-horizontal style="flex-grow: 1;">
                <div id="content">
                    <hb-route path="games" default>
                        <hb-games-page></hb-games-page>
                    </hb-route>
                    
                    <hb-route path="events">
                        <hb-events-page></hb-events-page>
                    </hb-route>
                    
                    <hb-route path="settings">
                        <hb-settings-page></hb-settings-page>
                    </hb-route>
                </div>
                <div id="snackbar-container"></div>
            </x-scroll-box>
            
            <x-bottom-navigation>
                <x-bare-link>
                    <a href="#games">
                        <x-bottom-navigation-action svg="/icons/baseball.svg" id="games-page">
                        </x-bottom-navigation-action>
                    </a>
                </x-bare-link>
                
                <x-bare-link>
                    <a href="#events">
                        <x-bottom-navigation-action svg="/icons/calendar-dots.svg" id="events-page">
                        </x-bottom-navigation-action>
                    </a>
                </x-bare-link>
                
                <x-bare-link>
                    <a href="#settings">
                        <x-bottom-navigation-action svg="/icons/dots-three-circle.svg" id="settings-page">
                        </x-bottom-navigation-action>
                    </a>
                </x-bare-link>
            </x-bottom-navigation>
        `;

        this.snackbarContainer = this.shadow.querySelector('#snackbar-container')! as HTMLDivElement;
    }

    connectedCallback() {
        setTimeout(() => {
            const splash = this.shadow.querySelector('#splash');
            if (splash) {
                // Trigger fade-out
                splash.classList.add('fade-out');

                // Remove splash and restore pointer events and scrolling after transition ends
                splash.addEventListener('transitionend', () => {
                    splash.remove();
                    this.removeAttribute('style'); // Restore scrolling
                });
            }
        }, 2000); // delay before splash fades out

        this.networkStatusListener();
        this.activeBottomNavigation();
    }

    private activeBottomNavigation(): void {
        const links = this.shadow.querySelectorAll<HTMLLinkElement>('x-bottom-navigation > x-bare-link > a');
    
        if (links.length === 0) {
            return;
        }
 
    
        // Function to update the active state
        const updateActiveState = () => {
            const activeRoute = Router.current();
    
            for (const link of links) {
                const linkRoute = link.href.split('#')?.[1];
                const action = link.querySelector<BottomNavigationAction>('x-bottom-navigation-action');
    
                if (action) {
                    if (linkRoute === activeRoute) {
                        action.setAttribute('active', '');
                    } else {
                        action.removeAttribute('active');
                    }
                }
            }
        };
    
        // Ensure active state on initial load
        const initializeState = () => {
            const currentHash = Router.current();
    
            // If no hash is present or invalid, set to default route
            if (!currentHash) {
                window.location.hash = `#games`;
            }
    
            updateActiveState();
        };
    
        // Add listeners for hashchange and popstate
        window.addEventListener('hashchange', updateActiveState);
        window.addEventListener('popstate', updateActiveState);
    
        // Initialize the active state
        initializeState();
    }
    
     private networkStatusListener() {
        window.addEventListener('online', () => {
            const existingSnackbar = this.snackbarContainer.querySelector('#offline-status') as Snackbar | null;

            if (existingSnackbar) {
                existingSnackbar.removeAttribute("visible");
            }
        });

        window.addEventListener('offline', () => {
            let existingSnackbar = this.snackbarContainer.querySelector('#offline-status') as Snackbar | null;

            if (!existingSnackbar) {
                this.snackbarContainer.innerHTML = `
                    <x-snackbar id="offline-status" position="left" stretch-horizontal fixed offset="bottom-navigation" closable>
                        <x-typography display="body1">${translate('app.offline')}</x-typography>
                    </x-snackbar>
                `;

                existingSnackbar = this.snackbarContainer.querySelector('#offline-status') as Snackbar | null
            }

            ;

            existingSnackbar.setAttribute("visible", "");
        });
    }

}
