import type { League } from "../model/League"
import {fetchApi} from "../fetch.ts";

type ApiLeague = {
    slug: string;
    name: string;
    shortName: string;
};


export class LeagueRepository {
    public static async findAll(): Promise<League[]> {
        const apiLeagues = (await fetchApi(`/api/leagues.json`, () => [])) as ApiLeague[];

        return apiLeagues.map(v  => {
            return {
                id: v.slug,
                name: v.name,
                shortName: v.shortName
            }
        })
    }

    public static async findById(id: string): Promise<League> {
        const league = (await this.findAll()).find((league) => league.id === id)

        if (!league) {
            throw new Error(`League ${id} not found`)
        }

        return league
    }
}
